.info {
  position: relative;
  display: flex;
  align-items: center;
  align-content: center;
  justify-content: space-between;
  width: 100%;
  margin-top: 51px;
  margin-bottom: 0;
  border-radius: 0;
  background-color: @color-red;
  box-shadow: none;
  @media screen and (max-width: 1699.98px) {
    padding-left: 10%;
  }
  @media screen and (max-width: 1211.98px) {
    padding-left: 7%;
  }
  @media screen and (max-width: 991.98px) {
    padding-left: 62px;
  }
  @media (max-width: @tablet-width) {
    padding-left: 0;
  }
  @media screen and (max-width: 540.98px) {
    padding: 10px 20px 10px 25px;
  }
  @media screen and (max-width: 499.98px) {
    display: block;
    padding: 15px 10px;
  }
  @media screen and (min-width: 360px) {
    margin-top: 70px;
  }
  @media screen and (min-width: 541px) {
    padding: 22px 35px 22px 25px;
  }
  @media screen and (min-width: 1280px) {
    padding-right: 25px;
    padding-left: 20.5rem;
    -moz-padding-start: 23.5rem;
  }
  @media screen and (max-width: 575.98px) and (max-height : 451px) {
    margin-top: 51px;
  }

  &:hover {
    box-shadow: none;
  }

  & ~ .main {
    margin-top: 40px;

    & .main-page {
      @media screen and (max-width: 1279.98px) {
        margin-top: 0;
      }
    }

    &__container-fluid {
      margin-top: 0;
      padding-top: 0;
    }
  }

  &__text {
    max-width: 100%;
    margin-top: 0;
    margin-bottom: 0;
    padding-right: 15px;
    font-size: 14px;
    font-weight: 500;
    color: @white;
    text-align: left;
    word-break: normal;
    @media screen and (max-width: 575.98px) {
      padding-left: 0;
    }
    @media screen and (max-width: 499.98px) {
      padding-right: 0;
      padding-bottom: 13px;
      text-align: center;
      line-height: 1.3em;
    }
    @media screen and (min-width: 400px) {
      font-size: 16px;
    }
    @media screen and (min-width: 541px) {
      font-size: 18px;
    }
    @media screen and (min-width: 576px) {
      padding-left: 5px;
      font-size: 20px;
    }
    @media screen and (min-width: 1280px) {
      padding-left: 20px;
    }
  }

  &__button {
    display: inline-flex;
    align-content: center;
    align-items: center;
    justify-content: center;
    max-width: 132px;
    padding: 8px 17px;
    border: none;
    outline: none;
    font-weight: 600;
    text-transform: uppercase;
    color: @color-red;
    text-shadow: 0 1px 0 @white-rgba-four;
    @media screen and (max-width: 540.98px) {
      display: flex;
      align-items: center;
      max-width: 125px;
      font-size: 14px;
    }
    @media screen and (max-width: 499.98px) {
      width: 100%;
      max-width: max-content;
      margin: auto;
      min-height: auto;
      padding: 15px;
    }
    @media screen and (max-width: 399.98px) {
      font-size: 12px;
    }

    &:active {
      box-shadow: inset 0 24px 24px darken(@grey-sixteen, 3%);
      background: darken(@grey-seventeen, 10%);
    }
  }

  & ~ .beta-message {
    margin-top: 0;
  }
}
