.popover {
  max-width: calc(100% - 20px);
  width: 100%;
  border-radius: 10px;
  font-family: @montserrat-font;
  font-size: 12px;
  @media screen and (min-width: 480px) {
    font-size: 14px;
    max-width: 400px;
  }
  @media screen and (max-width: 854px) and (orientation : landscape) {
    font-size: 12px;
  }

  &__wrapper {
    border-radius: 25px;
    background-color: @white;
    box-shadow: 0 28px 59px @black-rgba-two,
    0 40px 120px @black-rgba-five;
  }

  &__default-style-none {
    padding: 0 10px;
    border: none;
    background-color: transparent;
    box-shadow: none;
  }

  &__md {
    max-width: 560px;

    &.bottom {
      & .arrow {
        &:after {
          border-bottom-color: @color-green;
        }
      }
    }

    & .popover-title {
      padding-top: 10px;
      padding-bottom: 10px;
      border-radius: 25px 25px 0 0;
      @media screen and (min-width: 480px) {
        padding-top: 20px;
        padding-bottom: 20px;
      }
      @media screen and (max-width: 854px) and (orientation : landscape) {
        padding-top: 10px;
        padding-bottom: 10px;
      }
    }

    & .popover-content {
      padding-top: 10px;
      @media screen and (min-width: 480px) {
        padding-top: 20px;
      }
      @media screen and (max-width: 854px) and (orientation : landscape) {
        padding-top: 10px;
      }
    }

    & .btn {
      @media screen and (min-width: 576px) {
        font-size: 14px;
      }
    }
  }

  &[class*="tour-"] {
    & .btn {
      &-default {
        border: none;
        border-radius: 5px;
        background: @green-five;
        box-shadow: inset 0 24px 24px transparent, inset 0 -30px 30px transparent;
        font-family: @montserrat-font;
        font-weight: 400;
        letter-spacing: 0;
        color: @white;
        transition: 0.3s;

        &:hover,
        &:active,
        &:focus {
          background: @green-five;
          box-shadow: inset 0 24px 24px @green-eight,
          inset 0 -30px 30px @green-nine;
          border: none;
          color: @white;
          transition: 0.3s;
        }

        &:disabled {
          pointer-events: none;
          opacity: 0.3;
        }
      }

      &[data-role='prev'] {
        border-top-right-radius: 0;
        border-bottom-right-radius: 0;
      }

      &[data-role='next'] {
        border-top-left-radius: 0;
        border-bottom-left-radius: 0;
      }

      &[data-role='end'] {
        background: linear-gradient(180deg, @color-red 0%, #b22c1f 100%);
        box-shadow: inset 0 24px 24px transparent, inset 0 -30px 30px transparent;
        color: @white;

        &:hover,
        &:active,
        &:focus {
          background: linear-gradient(180deg, @color-red 0%, #b22c1f 100%);
          box-shadow: inset 0 24px 24px @red-two, inset 0 -30px 30px @red-three;
          color: @white;
        }

        &.btn-d-block {
          display: inline-block;
        }

        &.green {
          background: @green-five;
          box-shadow: inset 0 24px 24px transparent, inset 0 -30px 30px transparent;

          &:hover,
          &:active,
          &:focus {
            background: @green-five;
            box-shadow: inset 0 24px 24px @green-eight,
            inset 0 -30px 30px @green-nine;
            border: none;
            color: @white;
            transition: 0.3s;
          }
        }
      }
    }

    & .popover-navigation {
      border-top: 1px solid @grey-twenty-nine;

      &__height {
        &_middle {
          padding-top: 10px;
          padding-bottom: 10px;
          text-align: center;
          @media screen and (min-width: 480px) {
            padding-top: 20px;
            padding-bottom: 20px;
          }
          @media screen and (max-width: 854px) and (orientation : landscape) {
            padding-top: 10px;
            padding-bottom: 10px;
          }

          & .btn {
            margin-right: 10px;
            margin-left: 10px;
            padding: 10px 20px;
            border-radius: 10px;
          }

          & *[data-role="end"] {
            float: none;
          }
        }
      }
    }
  }

  &[class*="tour-"]&[class*="-reflex"] {
    & .btn {
      &[data-role='next'] {
        cursor: default;
        pointer-events: none;
        opacity: 0.3;
      }
    }
  }

  &-title {
    padding-top: 10px;
    padding-bottom: 10px;
    font-weight: 500;

    &__bg {
      &_green {
        border: none;
        background-color: @color-green;
      }
    }

    &_middle {
      font-size: 16px;
      @media screen and (min-width: 480px) {
        font-size: 22px;
      }
      @media screen and (max-width: 854px) and (orientation : landscape) {
        font-size: 16px;
      }
      @media screen and (max-width: 570px) and (orientation : landscape) {
        font-size: 14px;
      }
    }

    &_center {
      text-align: center;
    }

    &__color {
      &_white {
        color: @white;
      }
    }
  }

  &-content {
    &__middle {
      padding-right: 15px;
      padding-left: 15px;
      font-weight: 500;
      @media screen and (min-width: 480px) {
        padding-right: 25px;
        padding-left: 25px;
        font-size: 18px;
      }
      @media screen and (max-width: 854px) and (orientation : landscape) {
        padding-right: 15px;
        padding-left: 15px;
        font-size: 12px;
      }
    }

    span {
      display: block;
      padding-bottom: 20px;
    }

    ul {
      margin-top: 5px;
      margin-bottom: 5px;
      padding-left: 16px;
    }
  }
}
